import './App.css';
import React from 'react';
import { elementData } from './elements.js';

// nonmetal, metalloid, halogen, ...
var unique_categories = new Set(Object.values(elementData.element_category))

// maps each element category to a set of atomic numbers
var category_map = {}
for (const cat of unique_categories) {
  category_map[cat] = new Set()
}
for (const [key, value] of Object.entries(elementData.element_category)) {
  category_map[value].add(key)
}

class Element extends React.Component {
  
  render() {
    var number = this.props.atomicNumber
    var category = elementData.element_category[number]
    if (this.props.isVisible.has('all') || this.props.isVisible.has(category)) {
      return (
        <div className={'element ' + category}>
          <div className='symbol'>{elementData.symbol[number]}</div>
          <div className='atomic-number'>{elementData.atomic_number[number]}</div>
          <div className='name'>{elementData.name[number]}</div>
          <div className='atomic-mass'>{elementData.atomic_weight[number].toPrecision(5)}</div>
        </div>
      )
    } else {
      return <div className='blank-card'></div>
    }
  }
}

class PeriodicTable extends React.Component {
  constructor(props) {
    super(props)
    this.setVisible = this.setVisible.bind(this)
    this.state = {
      visible: new Set(['all']),
    }
  }


  // Yes, this method looks complicated
  // It just lays out the periodic table, row by row, accounting for the 'gaps'
  makeElements() {
    var visible = this.state.visible
    var rows = [[]]
    for (var i=0; i<2; i++) {
      rows[0].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows[0].splice(1, 0, <div className="gap-1">&nbsp;</div>)
    rows.push([])
    for (i=2; i<4; i++) {
      rows[1].push(<Element atomicNumber = {i} isVisible={visible}/>)
    }
    rows[1].push(<div className="gap-2">&nbsp;</div>)
    for (i=4; i<10; i++) {
      rows[1].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows.push([])
    for (i=10; i<12; i++) {
      rows[2].push(<Element atomicNumber={i} isVisible={visible}/>)
    } 
    rows[2].push(<div className="gap-2">&nbsp;</div>)
    for (i=12; i<18; i++) {
      rows[2].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows.push([])
    for (i=18; i<36; i++) {
      rows[3].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows.push([])
    for (i=36; i<54; i++) {
      rows[4].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows.push([])
    for (i=54; i<56; i++) {
      rows[5].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows[5].push(<div className='element'>
      <div className='placeholder lanthanide'>Lanthanides</div></div>)

    for (i=71; i<86; i++) {
      rows[5].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows.push([])
    for (i=86; i<88; i++) {
      rows[6].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows[6].push(<div className='element'>
    <div className='placeholder actinide'>Actinides</div></div>)

    for (i=103; i<118; i++) {
      rows[6].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    rows.push([])
    rows[7].push(<div className='gap-3'>&nbsp;</div>)

    rows.push([])
    rows[8].push(<div className='gap-4'>&nbsp;</div>)
    for (i=56; i<71; i++) {
      rows[8].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    
    rows.push([])
    rows[9].push(<div className='gap-4'>&nbsp;</div>)
    for (i=88; i<103; i++) {
      rows[9].push(<Element atomicNumber={i} isVisible={visible}/>)
    }
    return rows
  }

  setVisible(event) {
    var category = event.target.innerHTML
    var visible = this.state.visible
    if (this.state.visible.has(category)) {
      visible.delete(category)
      this.setState({visible: visible})
    } else if (category === 'all') {
      visible.clear()
      visible.add(category)
      this.setState({visible: visible})
    } else {
      if (this.state.visible.has('all')) {
        visible.delete('all')
      }
      visible.add(category)
      this.setState({visible: visible})
    }
  }

  makeAllButton() {
    var classes = []
    classes.push('choose-element')
    classes.push('all-button')
    if (this.state.visible.has('all')) {
      classes.push('is-clicked')
    }
    var button = <button className={classes.join(' ')}
    onClick={(e) => {this.setVisible(e)}}>all</button>
    return button
  }

  makeButtons() {
    var buttons = []
    buttons.push(this.makeAllButton())
    for (var category of unique_categories) {
      var classes = []
      classes.push('choose-element')
      classes.push(category + '-button')
      if (this.state.visible.has(category)) {
        classes.push('is-clicked')
      }
      var button = <button className={classes.join(' ')}
      onClick={(e) => {this.setVisible(e)}}>{category}</button>
      buttons.push(button)
    }
    return <div className='button-container'>{buttons}</div>
  }

  render() {
    var elements = this.makeElements()
    var buttons = this.makeButtons()
    return <>
    {buttons}
    {elements}
    </>
  }
}

function App() {
  return (
    <PeriodicTable />
  )
}

export default App;
